import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import { withStyles } from '@material-ui/core/styles';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';


const styles = theme => ({
    root: {
        width: '100%',
        overflow: 'none',
    },
});


class TakenTrades extends React.Component {

    constructor(props) {
        super(props)
        this.getAllResultValues = this.getAllResultValues.bind(this);
        this.createData = this.createData.bind(this);

        this.state = {
            resultsList: [],
            totalPairs: 0,
            isFetching: true,
            hasHttpError: false,
            errorMessage: '',
            hasPendingHttp: false,
            blocking: false,
        };
    }

    dateToYMD(date) {
        var strArray = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        var d = date.getDate();
        var m = strArray[date.getMonth()];
        var y = date.getFullYear();
        var t = date.toLocaleTimeString();
        return '' + (d <= 9 ? '0' + d : d) + '-' + m + '-' + y + ', ' + t;
    }

    getTimeframe(timeframe) {
        if (timeframe === 'PERIOD_D1') {
            return 'Daily';
        } else if (timeframe === 'PERIOD_W1') {
            return 'Weekly';
        } else {
            return timeframe;
        }
    }

    createData(id, date, symbol, timeframePeriod, isBullish, isOpen, lowestLossReached, highestProfitReached, finalCloseProfit, lowestPriceReached, highestPriceReached, passedFibboLevel) {
        let orderType = (isBullish === true) ? "BUY" : "SELL";
        let status = (isOpen === true) ? "OPEN" : "CLOSED";
        let dateString = this.dateToYMD(new Date(date));
        let timeframe = this.getTimeframe(timeframePeriod);
        return { id, symbol, dateString, timeframe, orderType, status, lowestLossReached, highestProfitReached, finalCloseProfit, lowestPriceReached, highestPriceReached, passedFibboLevel };
    }


    render() {

        if (this.state.isFetching) return <Loading />;
        if (this.state.hasHttpError)
            return <PaperSheet color="error" header={this.props.timeframe} message={this.state.errorMessage} />;

        const rows = [];
        let entry = {};
        for (let index = 0; index < this.state.resultsList.length; ++index) {
            entry = this.state.resultsList[index];
            rows.push(this.createData(entry.id, entry.dateAdded, entry.symbol, entry.openTimeframe, entry.isBullish, entry.isOpen, entry.lowestLossReached, entry.highestProfitReached, entry.finalCloseProfit, entry.lowestPriceReached, entry.highestPriceReached, entry.passedFibboLevel));
        }

        return (
            <TableContainer component={Paper}>
                <Table aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Date</TableCell>
                            <TableCell>Symbol</TableCell>
                            <TableCell align="right">Timeframe</TableCell>
                            <TableCell align="right">Type</TableCell>
                            <TableCell align="right">Status</TableCell>
                            <TableCell align="right">Lowest Loss Reached</TableCell>
                            <TableCell align="right">Highest Profit Reached</TableCell>
                            <TableCell align="right">Final Close Profit</TableCell>
                            <TableCell align="right">Lowest Price Reached</TableCell>
                            <TableCell align="right">Highest Price Reached</TableCell>
                            <TableCell align="right">Passed Fibbo Level</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow key={row.id}>
                                <TableCell component="th" scope="row">
                                    {row.dateString}
                                </TableCell>
                                <TableCell align="right">{row.symbol}</TableCell>
                                <TableCell align="right">{row.timeframe}</TableCell>
                                <TableCell align="right">{row.orderType}</TableCell>
                                <TableCell align="right">{row.status}</TableCell>
                                <TableCell align="right">{row.lowestLossReached}</TableCell>
                                <TableCell align="right">{row.highestProfitReached}</TableCell>
                                <TableCell align="right">{row.finalCloseProfit}</TableCell>
                                <TableCell align="right">{row.lowestPriceReached}</TableCell>
                                <TableCell align="right">{row.highestPriceReached}</TableCell>
                                <TableCell align="right">{row.passedFibboLevel}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        );

    }

    getAllResultValues() {
        var url = BACKEND_BASE_URL + 'Trader/TradesTracker';

        this.setState({
            isFetching: true,
            hasHttpError: false,
            errorMessage: '',
            resultsList: []
        });
        fetch(url)
            .then(response => response.json())
            .then(data => {
                this.setState({
                    resultsList: data,
                    totalPairs: data.length,
                    isFetching: false,
                    hasHttpError: false
                });
            }).catch((error) => {
                this.setState({
                    isFetching: false,
                    hasHttpError: true,
                    errorMessage: error.toString()
                });

            });
    }
    componentDidMount() {
        this.getAllResultValues();
    }

}

export default TakenTrades;